import {fk1tofk5, radec2xy} from './projections.js';
import d3 from 'd3'

const d2r = Math.PI/180;

let boundaries= [];

export const calcBoundaries = (projection, azOff, config) =>{
  boundaries=[]
	var posa,posb,a,b,l,d,atob,btoa,move,j,ra,dc,dra,ddc,points;
	atob = "";
	btoa = "";
	var n = 5;
	var cbdone = [];

  boundariesDefinition.forEach((boundarie, i) => {
		points = [];
				for(l = 1; l < boundarie.length; l+=2){
					b = [boundarie[l], boundarie[l+1]];
					if(a){
						atob = a[0]+','+a[1]+'-'+b[0]+','+b[1];
						btoa = b[0]+','+b[1]+'-'+a[0]+','+a[1];
					}
					if(l > 1){
						move = (cbdone[atob] || cbdone[btoa]);
						if(typeof move==="undefined") move = true;
						ra = (b[0]-a[0])%360;
						if(ra > 180) ra = ra-360;
						if(ra < -180) ra = ra+360;
						dc = (b[1]-a[1]);

						// If we've already done this line we'll only calculate
						// two points on the line otherwise we'll do 5
						n = (move) ? 5 : 2;
						if(ra/2 > n) n = parseInt(ra);
						if(dc/2 > n) n = parseInt(dc);

						dra = ra/n;
						ddc = dc/n;

						for(let i = 1; i <= n; i++){
							ra = a[0]+(i*dra);
							if(ra < 0) ra += 360;
							dc = a[1]+(i*ddc);
							// Convert to J2000
							d = fk1tofk5(ra*d2r,dc*d2r);
							points.push({x:d[0], y:d[1],move:move});
						}
					}
					// Mark this line as drawn
					cbdone[atob] = true;
					cbdone[btoa] = true;
					a = b;
				}
				posa = null;
				// Now loop over joining the points
				for(let i = 0; i <= points.length; i++){
					j = (i === points.length) ? 0 : i;
          posb = radec2xy(points[j].x, points[j].y, projection, azOff, config);
					if(posa && (isVisible(posa.el) || isVisible(posb.el)) && points[j].move){
						if(!isPointBad(posa) && !isPointBad(posb)){
		    			boundaries.push({posa, posb});
						}
					}
					posa = posb;
				}
  });
}


export const drawBoundaries = (svg, config) =>{
  const bb = boundaries.filter(b=>isVisible2(b.posa, config) && isVisible2(b.posb, config));

  const lines = svg.selectAll('.constellationBoundaries');
  const databoundBoundaries = lines.data(bb);
  databoundBoundaries.enter().append('path').attr('class','constellationBoundaries');;
  databoundBoundaries.exit().remove();
  databoundBoundaries
      .attr("d", d=>lineFunction([d.posa, d.posb]))
      .attr("stroke", "#ff84")
      .attr("stroke-width", 1)
      .attr("fill", "none");
}

const lineFunction = d3.svg.line()
     .x(d =>{ return d.x; })
     .y(d =>{ return d.y; })
     .interpolate("linear");

const isVisible = (el) =>{
  return el>=0;
};
const isVisible2 = (b, config) =>{
  return b.x>-100 && b.x<config.width+100 && b.y>-100 & b.y<config.height+100;
};
const isPointBad = (p) =>{
	return typeof p !== "object";
};

